<template>
  <div class="productCenter">
    <div id="center">
      <!--<div class="smallTitle1">-->
      <!--<ul>-->
      <!--<li>-->
      <!--<p>国内少数指纹锁核心技术拥有者</p>-->
      <!--</li>-->
      <!--<li>-->
      <!--<p>雅洁智能锁，80道工序全数合格检验，超强精准识别，权威体系认证。</p>-->
      <!--</li>-->
      <!--</ul>-->
      <!--</div>-->
      <div class="banner">
        <img src="../../static/img/banner01.png" alt="">
      </div>
      <div class="twoProduce bodyBox">
        <div class="twoTitlePro">
          <div v-if="index == 1" class="ttBox" v-for="(item,index) in topBtn"><button :class="{'hasBtn':index==btnHas}" @click="btnclick(index,item.id,item.name)" :disabled="index==btnHas">{{item.name}}</button></div>
        </div>
        <div class="proSwiper proSwiper1">
          <div class="swiper-container swiper-container-1">
            <div class="swiper-wrapper">
              <div class="swiper-slide" v-for="(item,index) in switchBanner">
                <div class="swiperbox" @click="spr(index,item.id,item.name)">
                  <div class="swiperImg" :class="{'swiperImgRound':index==swiperImg}">
                    <img :src="item.pic"/>
                  </div>
                  <div class="swiperP">
                    <p>{{item.name}}</p>
                  </div>
                </div>
              </div>
            </div>
            <div class="swiper-pagination"></div>
          </div>
        </div>
      </div>
      <div class="searchInput bodyBox" style="background-color:white;margin-top: 0.4rem;display: none"  v-if="statusRecommend!=0">
        <form action="###" @submit="inputSearch" >
          <div class="searchBox" style="border: 0;">
            <input type="text" placeholder="请输入关键字" v-model="inputText"/>
          </div>
        </form>
      </div>
      <div class="proTitle bodyBox proTitle1" v-if="statusRecommend!=0">
        <p> {{nowName}}</p>
      </div>
      <div class="moreProChoice" v-if="statusRecommend!=0 && threeShow == 1">
        <button :class="{'btnChoice':btnIndex==index}" @click="threeType(index,item.id)" v-for="(item,index) in threeBtn">{{item.name}}</button>
        <div></div>
      </div>
      <div>
        <div class="proTitle bodyBox" v-if="statusRecommend==0"  >
          <p>{{myCommand}}</p>
        </div>
        <div class="proList bodyBox">
          <router-link :to="'/productDetails?pid='+item.id" class="proChild"  v-for="(item,index) in proList" :key="index">
            <div class="proImg">
              <img :src="item.pic" />
            </div>
            <div class="proP">
              <ul>
                <li>
                  <p>{{item.name}}</p>
                </li>
                <li>
                  <p>{{item.desc}}</p>
                </li>
                <li>
                  <p>MORE</p>
                </li>
              </ul>
            </div>
          </router-link>
          <!--<button   class="proMore" v-if=" statusRecommend==0&&twoRecommand[index].proLength>3 && twoRecommand[index].proLength > twoRecommand[index].limit" @click="twoRecommand[index].limit = twoRecommand[index].proLength">查看全部推荐</button>-->
        </div>
      </div>
      <div class="sorry bodyBox" v-if="prolength==0">
        <img src="../../static/img/icon03.png" alt="" style="width: 30%;margin-bottom: 0.26rem;">
        <p>暂无更多产品信息，感谢关注。</p>
      </div>
      <div class="sorry bodyBox" v-if="searchResult == 0">
        <img src="../../static/img/icon03.png" alt="" style="width: 30%;margin-bottom: 0.26rem;">
        <p>暂无更多产品信息，感谢关注。</p>
      </div>
      <div class="mybodyBox bodyBox"  v-if="lastPage>=2&&prolength!=0">
        <el-pagination
          background
          layout="prev, pager, next"
          :pager-count = 9
          :page-size = 'page.per_page'
          :total="page.total"
          @current-change = "sizeChange"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import index from "../router";

  export default {
    name: 'productCenter',
    data(){
      return{
        btnHas:1,

        switch1:[],
        switch2:[],
        twoRecommand:[],//推荐名称
        eid:-1,
        fid:-1,
        statusRecommend:0,
        proList:[],
        recommendPro1:[], //智能五金推荐
        recommendPro2:[], //家用五金推荐
        threeBtn:[],
        btnIndex:0,

        page:{
          total:0,
          per_page:0
        },
        lastPage:0,

        topBtn:[],//顶级分类
        topId:0,
        switchBanner:[],
        mySwiper:0,
        scdId:0,
        nowName:'',
        pageId:-1,//当前三级分类
        prolength:0,
        threeShow:1,
        myCommand:'传统五金推荐：',
        inputText:'',
        searchResult:1,
        swiperImg:-1
      }
    },
    mounted:function () {
      var  _this = this;
      //获取顶级分类
      this.$api.axiosGet('/index/product/getCates/pid/0',{},function (data) {
        //设置顶级分类
        for(var i=0; i < data.data.cates.length;i++){
          var attr = {
            name:data.data.cates[i].name,
            id:data.data.cates[i].id
          }
          _this.topBtn.push(attr);
        }
        //智能五金
        _this.topId = _this.topBtn[1].id;
        _this.myCommand = _this.topBtn[1].name +'推荐';
        _this.topShow();
      });
    },
    methods:{
      topShow:function () {
        var _this = this;
        //获取二级分类
        _this.$api.axiosGet('/index/product/getCates/pid/'+_this.topId,{},function (data) {
          _this.switchBanner = [];
          let mydata = data.data.cates.all;
          for(var i=0; i<mydata.length;i++)
          {
            var icon = {
              id:mydata[i].id,
              name:mydata[i].name,
              pic:_this.$baseLink+mydata[i].pic,
              limit:3
            };
            _this.switchBanner.push(icon);
          }

          setTimeout(function () {
            if(_this.mySwiper==0)
            {
              _this.mySwiper = new Swiper('.swiper-container-1', {
                slidesPerView : 3,
                slidesPerGroup : 3,
                autoplay:2000,
                speed:1000,
                pagination : '.swiper-pagination',
                observer:true,
                autoplayDisableOnInteraction:false
              });
            }else{
              _this.mySwiper.update();
            }

          },100);
        });
        //推荐
        _this.$api.axiosGet('/index/product/getProductList/type/limit3/id/'+_this.topId,{},function (data) {
          _this.proList = [];
          _this.proList = data.data.list.data;
          _this.prolength = _this.proList.length;
          for(var i=0; i<_this.proList.length; i++)
          {
            _this.proList[i].pic = _this.$baseLink+_this.proList[i].pic;
          }
          _this.lastPage = data.data.list.last_page;
          _this.page.total = data.data.list.total;
          _this.page.per_page = data.data.list.per_page;
        });
      },
      //切换
      btnclick:function (index,id,name) {
        var _this = this;
        _this.btnHas = index;
        _this.statusRecommend = 0;
        _this.topId = id;
        _this.myCommand = name+'推荐：';
        _this.swiperImg = -1;
        _this.topShow();
        setTimeout(function () {
          _this.mySwiper.slideTo(0,2000)
        },400)
      },
      spr:function (index,id,name) {
        var _this = this;
        _this.nowName = name;
        _this.statusRecommend = 1;
        _this.scdId = id;
        _this.swiperImg = index;
        _this.$api.axiosGet('/index/product/getCates/pid/'+_this.scdId,{},function (data) {
          if(data.data.cates.length != 0)
          {
            _this.threeBtn = data.data.cates.all;
            _this.btnIndex = 0;
            _this.threeBtn.splice(0,0,{
              name:'全部',
              id:-1
            });
            _this.threeShow = 1;
          }else{
            _this.threeShow = 0;
          }

        });
        _this.proList = [];
        // console.log(_this.scdId);
        _this.$api.axiosGet('/index/product/getProductList/type/limit9/id/'+_this.scdId,{},function (data) {
          _this.proList = data.data.list.data;
          _this.prolength = _this.proList.length;
          for(var i=0; i<_this.proList.length; i++)
          {
            _this.proList[i].pic = _this.$baseLink+_this.proList[i].pic;
          }
          _this.lastPage = data.data.list.last_page;
          _this.page.total = data.data.list.total;
          _this.page.per_page = data.data.list.per_page;
        });
      },
      threeType:function (index,id) {
        var _this = this;
        _this.btnIndex = index;
        _this.pageId = id;
        if(index == 0)
        {
          _this.proList = [];
          _this.$api.axiosGet('/index/product/getProductList/type/limit9/id/'+_this.scdId,{},function (data) {

            _this.proList = data.data.list.data;
            _this.prolength = _this.proList.length;
            for(var i=0; i<_this.proList.length; i++)
            {
              _this.proList[i].pic = _this.$baseLink+_this.proList[i].pic;
            }
            _this.lastPage = data.data.list.last_page;
            _this.page.total = data.data.list.total;
            _this.page.per_page = data.data.list.per_page;
          });
        }else{
          _this.proList = [];
          _this.$api.axiosGet('/index/product/getProductList/type/limit9/id/'+_this.pageId,{},function (data) {
            _this.proList = data.data.list.data;
            _this.prolength = _this.proList.length;
            for(var i=0; i<_this.proList.length; i++)
            {
              _this.proList[i].pic = _this.$baseLink+_this.proList[i].pic;
            }
            _this.lastPage = data.data.list.last_page;
            _this.page.total = data.data.list.total;
            _this.page.per_page = data.data.list.per_page;
          });
        }

      },
      sizeChange(e) {
        var _this = this;
        $(window).scrollTop(0);
        if(_this.statusRecommend==0)
        {
          _this.$api.axiosGet('/index/product/getProductList/type/limit3/id/'+_this.topId+'/page/'+e,{},function (data) {
            _this.proList = [];
            _this.proList = data.data.list.data;
            _this.prolength = _this.proList.length;
            for(var i=0; i<_this.proList.length; i++)
            {
              _this.proList[i].pic = _this.$baseLink+_this.proList[i].pic;
            }
            _this.lastPage = data.data.list.last_page;
            _this.page.total = data.data.list.total;
            _this.page.per_page = data.data.list.per_page;
          });
        }else{
          _this.proList = [];
          _this.$api.axiosGet('/index/product/getProductList/type/limit9/id/'+_this.scdId+'/page/'+e,{},function (data) {
            _this.proList = data.data.list.data;
            _this.prolength = _this.proList.length;
            for(var i=0; i<_this.proList.length; i++)
            {
              _this.proList[i].pic = _this.$baseLink+_this.proList[i].pic;
            }
            _this.lastPage = data.data.list.last_page;
            _this.page.total = data.data.list.total;
            _this.page.per_page = data.data.list.per_page;
          })
        }

      },
      inputSearch:function (e) {
        var _this = this;
        if(_this.inputText!='')
        {
          // console.log(_this.inputText);
          _this.$api.axiosGet('/index/product/getProductList/type/limit9/name/'+_this.inputText,{},function (data) {
            // console.log(data);
          });
        }
        return false;
      }

    },

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "../../static/css/prodect.css";
  .mybodyBox >div{
    background-color: white!important;
  }
</style>



